import React, { useEffect, useState, useCallback } from 'react';
import type { FormProps } from 'antd';
import { Button, Checkbox, Form, Input, List, message } from 'antd';
import { CLIENT_ID, HOST } from '../../constnat';



const NewsList = (props:any) => {
    const { rssId, queryInfo, rssType } = props;
    let queryNewsInterval: any = null
    const [news, setNews] = useState([
        // {
        //     aiSummary: "",
        //     aiSummaryStatus: "",
        //     link: "http://www.chinanews.com/cj/2024/08-17/10270492.shtml",
        //     summary: "817 ( )17202433",
        //     title: ""
        // }
    ]);


    const queryNews = useCallback(async ()=> {
            const deviceId = await CLIENT_ID()
            fetch(`${HOST}/rss/detail?id=${rssId}&clientId=${deviceId}&type=${rssType}`).then((res)=>{
                res.json().then((data)=>{
                    setNews(data.data)
                    // const waitingList = data.data.filter((item:any)=> item.aiSummaryStatus === 'waiting' || item.aiSummaryStatus === 'starting');
                })
            })
    }, [rssId, rssType])


    useEffect(()=>{
        if(rssId) {
            queryNews();
            queryNewsInterval = setInterval(()=> {
                queryNews()
            }, 3000)
        }
        return () => {
            console.log("clear interval")
            queryNewsInterval && clearTimeout(queryNewsInterval)
        }
    }, [rssId, rssType, queryNews])

    



    const renderItem = (item:any) => {
        return (
            <List.Item.Meta
                title={<a href={item.link}  target='_blank'>{item.title}</a>}
                description={renderAction(item)}
            />
        )
            
    }

    const createTask = async (link: string) => {
        const deviceId = await CLIENT_ID()
        fetch(`${HOST}/ai/create`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                url: link,
                clientId: deviceId
            }),
        }).then((res) => {
            res.json().then((data) => {
                if (data.success) {
                    queryNews()
                } else {
                    message.error(data.errorMsg)
                }
            })
        })

    }

    const renderAction = (item:any) => {
        const { aiSummary, aiSummaryStatus } = item;
        if(aiSummaryStatus === 'starting') {
            return <div>AI...</div>
        } else if(aiSummaryStatus === 'failed') {
            return <div>AI...<span onClick={()=>createTask(item.link)}style={{color: "#1677ff", cursor: 'pointer'}}>&nbsp;&nbsp;</span>AI</div>
        } else if(aiSummaryStatus && aiSummaryStatus !== 'finish') {
            return <div>AI...</div>
        } 
        if(aiSummary) {
            return <div>{aiSummary}</div>
        } else {
            return <div>AI<span onClick={()=>createTask(item.link)}style={{color: "#1677ff", cursor: 'pointer'}}>&nbsp;&nbsp;</span>AI</div>
        }
    }
    return (
        <List
        size="large"
        header={null}
        footer={null}
        bordered
        dataSource={news}
        renderItem={(item:any) => <List.Item>{renderItem(item)}</List.Item>}
        />
    );
}


export default NewsList;