import React from 'react';
import { Space, Table, Tag } from 'antd';
import type { TableProps } from 'antd';

interface DataType {
  key: string;
  name: string;
  address: string;
}



const data: DataType[] = [
  {
    key: '1',
    name: 'XiaoHui.com',
    address: 'https://www.xiaohui.com/rss/',
  },
];

const App = (props:any) => {
    const { callback } = props;
    const sub = async (item:any) => {
        
        callback && callback({
            title: item.name,
            url: item.address
        });
    }

    const columns: TableProps<DataType>['columns'] = [
        {
          title: '',
          dataIndex: 'name',
          key: 'name',
        },
        {
          title: '',
          dataIndex: 'address',
          key: 'address',
        },
        {
          title: '',
          key: 'action',
          render: (_, record) => (
            <Space size="middle">
              <a onClick={()=>sub(record)}></a>
            </Space>
          ),
        },
      ];
    return (
        <Table columns={columns} dataSource={data} />
    )
}

export default App;